.ant-menu-inline {
  --left-menu-width: 200px;
}

..ant-menu-inline-collapsed {
  --left-menu-width: 80px;
}

.cart {
  width: calc(100% - var(--left-menu-width));
  max-height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  padding: 20px 10px;
}

.cart__tables {
  display: flex;
  flex-flow: row nowrap;
}

.cart__order-table {
  width: 47.5%;
  margin-right: 5%;
}

.cart__order {
  height: 62vh;
}

.cart__buttons {
  display: flex;
  justify-content: space-between;
}

.cart__products-table {
  width: 47.5%;
}

.cart__sum {
  display: flex;
  justify-content: space-between;
}

.sum__item {
  width: 45%;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  height: 25vh;
}

.sum__item_flex-start {
  justify-content: flex-start;
}

.didolf {
  background-color: #e6f7ff;
}


.ant-table-thead th:nth-child(1) {
  width: 42px;
  padding: 16px 0px;
  text-align: center;
}

.ant-table-thead th:nth-child(3) {
  width: 51px;
  padding: 16px 0px;
  text-align: center;
}

.ant-table-row td:nth-child(4),
.ant-table-row td:nth-child(5) {
  width: 25px;
}


.ant-table-thead th:nth-child(4),
.ant-table-thead th:nth-child(5) {
  width: 53.5px;
}

