*/

.cart__order-table .ant-table-thead th:nth-child(1),
.cart__order-table .ant-table-thead th:nth-child(2) {
  width: 42px;
  padding: 16px 6px;
}

.cart__order-table .ant-table-thead th:nth-child(4) {
  width: 51px;
  padding: 16px 7px;
}

.cart__order-table .ant-table-thead th:nth-child(5),
.cart__order-table .ant-table-thead th:nth-child(6),
.cart__order-table .ant-table-thead th:nth-child(7) {
  padding: 16px 0px;
  width: 100px;
}
.cart__order-table .ant-table-row td:nth-child(2) {
  padding: 16px 17.5px;
}

.cart__order-table .ant-table-row td:nth-child(5),
.cart__order-table .ant-table-row td:nth-child(6), 
.cart__order-table .ant-table-row td:nth-child(7) {
  width: 100px;
  padding: 16px 0;
  text-align: center;}
